/* General styling */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f5f5f5;
}

/* App container */
.app {
  text-align: center;
  padding: 20px;
}

/* Search bar styling */
.search-bar input {
  padding: 10px;
  width: 80%;
  margin: 20px 0;
  font-size: 16px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

/* Movie list */
.movie-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 20px;
  padding: 0 10px;
  justify-items: center;
}

/* Individual movie item */
.movie-item {
  background-color: white;
  padding: 10px;
  border-radius: 5px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between; /* Ensures the content is spaced out */
  width: 200px;
  height: 384px;
  word-wrap: break-word;
}

/* Poster styling */
.movie-poster {
  width: 100%;
  height: 150px;
  object-fit: contain;
  border-radius: 5px;
  margin-bottom: 10px; /* Adjusted margin */
}

/* Movie details */
.movie-details h3 {
  font-size: 14px;
  color: #333;
  margin: 0; /* Remove margin to reduce space */
}

.movie-details p {
  font-size: 12px;
  color: #777;
  margin: 2px 0; /* Reduce margin to minimize space between lines */
  line-height: 1.2; /* Adjust line height to control the vertical space */
}

/* Button styling */
.edit-movie-button {
  padding: 8px;
  background-color: #007BFF;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 14px;
  width: 100%;
  max-width: 180px;
  margin-top: auto; /* Ensures the button is pushed to the bottom */
  margin-bottom: 15px; /* Adds space below the button */
}

.edit-movie-button:hover {
  background-color: #0056b3;
}

/* Help page */
.help-search {
  text-align: center;
  margin-top: 20px;
}

/* AddMovieForm Styles */
.add-movie-form {
  background-color: white;
  padding: 30px;
  max-width: 500px;
  margin: 50px auto;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.add-movie-form h2 {
  text-align: center;
  margin-bottom: 20px;
  font-size: 24px;
  color: #333;
}

.add-movie-form input,
.add-movie-form textarea {
  width: 100%;
  padding: 12px;
  margin-bottom: 15px;
  border-radius: 5px;
  border: 1px solid #ccc;
  font-size: 16px;
  background-color: #fafafa;
}

.add-movie-form input:focus,
.add-movie-form textarea:focus {
  border-color: #007BFF;
  outline: none;
}

.add-movie-form button {
  background-color: #007BFF;
  color: white;
  border: none;
  padding: 12px 20px;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  width: 100%;
}

.add-movie-form button:hover {
  background-color: #0056b3;
}

/* Delete button styling */
.delete-button {
  padding: 10px;
  background-color: red;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 14px;
  width: auto;
  margin-top: 10px;
}

.delete-button:hover {
  background-color: #d9534f;
}

.delete-movie-page {
  text-align: center;
  margin-top: 50px;
}

.delete-movie-page .movie-details {
  display: inline-block;
  background-color: #f4f4f4;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
  margin-bottom: 20px;
  width: 250px;
}

.delete-movie-page .movie-poster {
  width: 200px;
  height: 300px;
  margin-bottom: 15px;
  border-radius: 8px;
  object-fit: cover;
}

.delete-movie-page button {
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  margin-right: 10px;
}

.delete-movie-page button.delete {
  background-color: red;
  color: white;
}

.delete-movie-page button.cancel {
  background-color: gray;
  color: white;
}

/* DeleteMovieList styles */
.delete-movie-list {
  text-align: center;
}

.delete-movie-list .movie-item {
  display: inline-block;
  width: 250px;
  background-color: #f4f4f4;
  padding: 20px;
  margin-bottom: 20px;
  text-align: center;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.delete-movie-list .movie-poster {
  width: 200px;
  height: 300px;
  border-radius: 8px;
  object-fit: cover;
  margin-bottom: 10px;
}

.delete-movie-list .delete-button {
  padding: 10px;
  background-color: red;
  color: white;
  text-decoration: none;
  border-radius: 5px;
  display: inline-block;
}
